.ctrl{
    position: relative;
    margin: 0 auto;
    width: 100%;
    height: 230px;
    text-align: center;

  }
.progressCircle{ 
    height: 250px;
      /* padding-bottom: 300px;  */
      position: relative;
  }
svg {
    height: 250px;
    width: 250px;
    position: relative;
    transform: rotate(-90deg);
    top: 0;
    left: 0;
    overflow: visible;
    filter: contrast(1.5);
}
.circle {
    fill: #00000000;
    stroke-width: 1;
    stroke-linecap: round;
    
}


.base{
    fill: #f9f9f900;
}
.over{
    fill: hsla(203, 91%, 22%, 0.712);
    stroke-width: 1;
    stroke:rgba(35, 34, 53, 0.164);
}

.pie {
    animation-name: pie;
}  

.svg-text{
    color: rgb(244, 245, 248);
    position: absolute;
    font-weight: 300;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    font-size: 1.2em;
}
.svg-text p{
    padding: 5px 0;
}


.athanplay{
    position: absolute;

    bottom: 0
}


@-webkit-keyframes pie {
    50%,100% { stroke-dasharray: 157,157,0,0;}
}
@keyframes pie {
    50%,100% { stroke-dasharray: 157,157,0,0;}
}
  
  .progressBarInfo{
    font-size:18px;
    color:#00bcac;
  }