.qiblah{
    position: absolute;
    top: 0;
    left: 0;
    display: flex;
    height: 100vh;
    width: 100vw;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    color: white;
}
.qiblah h2{
    margin-bottom: 100px;
}
.qiblah h2 span{
    color: yellowgreen;
    font-size: 120%;
    text-decoration: underline;
}
.qiblah img{
    width:60%
}

.center{
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}
.qiblah .n, .qiblah .e, .qiblah .s, .qiblah .w{
    color: white;
    font-size: 1.5em;
}

.qiblah i{
    color: white;
    font-size: 3em;
    padding: 50px;
    border: 1px solid white;
    border-radius: 50%;
    margin: 5px;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: -1;
}


#boussole{
   transform: rotate(0deg)
}
#eguille{
    transform-origin: center;
}