.coranPage{
    color: rgba(255, 255, 255, 0.986);
    line-height: 1.8em;
    font-size: 1.2em;
    font-family: "MeQuran";
    padding: 60px 0;
    min-height: 100vh;
}
.coranAyah{
    display: inline;
}
.sepparation{
    line-height: 0.8em;
}
.surah_name{
    padding: 18px 0;
    font-weight: 500;
    font-size: 1.2em;
    width: 100%;
    color: rgb(80, 26, 26);
}

.coran_nav .button_left{
    position: absolute;
    bottom: 10px;

    left: 20px;
    transform: translateY(-50%);
}
.coran_nav .button_right{
    position: absolute;
    bottom: 10px;
    right: 20px;
    transform: translateY(-50%);
}
.coran_nav span{
    position: absolute;
    bottom: 10px;
    left: 50%;
    transform: translate(-50%, -50%);
    color: rgba(255, 255, 255, 0.596);
}
.coran_nav .button_right, .coran_nav .button_left{
    background-color: transparent;
    border-radius: 50%;
    border: none;
    cursor: pointer;
}
.coran_nav .button_right i, .coran_nav .button_left i{
    font-size: 1.8em;
    color: rgba(255, 255, 255, 0.596);
}