@font-face {
  font-family: 'MeQuran';
  src: url('./assets/fonts/me_quran-webfont.ttf')
}
.App {
  text-align: center;
  position: relative;
  min-height: 100vh;
}

.main {background: linear-gradient(45deg, rgb(7, 46, 114), rgb(75, 130, 233));
  position: relative;
  min-height: 100vh;
  z-index: 1;
  top: 0;
  left: 0;
  width: 60vw;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media screen and (max-width: 990px){
  .App{
      width: 100vw
    }
  .App-link {
    color: #61dafb;
    }
    .main {
  position: relative;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100vw;
}
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
