.setting_{
    position: absolute;
    width: 100%;
    padding: 70px 8px;
    margin: 5px;
    border-radius: 5px;
    color: rgb(238, 231, 231);
    min-height: 100vh;
    background-color:rgb(7, 46, 114);
}
.setting-title{
    font-size: 1.5em;
    font-weight: 700;
    text-align: center;
}
hr{
    background-color: rgba(128, 128, 128, 0.397);
}

.form-group{
    width: 100%;
    padding: 8px;
}
.form-group label{
    display: block;
}
.form-group input{
    width: 100%;
    background-color: transparent;
    line-height: 30px;
    border: none;
    text-align: center;
    margin-top: 8px;
    color: rgb(236, 226, 226);
    font-size: 1.2em;
    border-bottom: 1px solid white;
}
.form-group select{
    width: 90%;
    background-color: transparent;
    line-height: 30px;
    border: none;
    text-align: center;
    margin-top: 8px;
    color: white;
    font-size: 1.2em;
    border-bottom: 1px solid rgb(231, 224, 224);
}
.form-group select option{
    color: rgb(7, 46, 114);
}
hr{
    margin: 30px 0;
    height: 3px;
    border: none;
    background-color: rgb(5, 151, 13)
}

.p-geo-enter{
    padding: 10px;
}
.p-geo{
    text-align: center;
}

.select-contry{
    background-color: none;
    border: 1px solid grey;
    border-radius: 5px;
    height: 25px;
}

.geolocation{
    display: flex;
    justify-content: space-around;
    align-items: center;
}


.b-location{
    background-color: rgba(0, 0, 0, 0);
    border: 0;
    vertical-align: middle;
}
.b-location_{
    background-color: rgba(0, 0, 0, 0);
    border: 0;
    vertical-align: middle;
}


.switcher{
    width: 50px;
    height: 18px;
    background-color: grey;
    border-radius: 10px;
    transition: all 0.3s ease;
    display: flex;
    align-items: center;
    justify-content: flex-start;
}
.switcher2{
    width: 50px;
    height: 18px;
    background-color: rgb(14, 168, 47);
    border-radius: 10px;
    transition: all 0.3s ease;
    display: flex;
    align-items: center;
    justify-content: flex-end;
}
.switcher i, .switcher2 i{
    width: 20px;
    height: 20px;
    margin: 3px;
    border-radius: 50%;
    background-color: rgb(227, 238, 227);
    font-size: 18px;
    line-height: 20px;
    font-weight: 800;
}

.valider-setting{
    width: 100%;
    background-color: transparent;
    color: rgb(238, 227, 227);
    border: none;
    font-size: 400;
    font-size: 1.3em;
    margin-top: 50px;
    cursor: pointer;
}

.minutes{
    padding: 25px 0;
}
.minutes h4{
    padding: 10px 0;
    
}

.range{ 
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.range_field{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    
}
.range_field input{
    width: 60%;
    
}
.range_field span{
    margin: 8px;
    font-size: 0.8em;
}

.range_field input:active, .range_field input:focus,.range_field input:valid{
    outline: none;
}
.adresse_details{
    padding: 20px ;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}
.adresse_details p{
    margin: 8px;
}
.adresse_details p strong{
    color: lawngreen;
}
.setting_section{
    color: rgb(57, 51, 58);
    font-size: 1.3em;
    font-weight: 800;
    width: 100%;
    margin: 10px ;
}
.valider_setting{
    background-color: rgb(22, 116, 3);
    
    border: none;
    font-size: 400;
    display: inline;
    font-size: 1.3em;
    padding: 5px 10px;
    border-radius: 10px;
    cursor: pointer;
}
.valider_setting a{
    text-decoration: none;
    color: rgb(235, 224, 224);
}
.fa-check{
    color: rgb(4, 138, 15);
    transform: scale(1);
}