.calendar{
    padding:60px 8px 30px;
}
.calendar h3{
    padding: 10px;
    color: white;
}

#customers {
    font-family: "Trebuchet MS", Arial, Helvetica, sans-serif;
    border-collapse: collapse;
    width: 100%;
  }
  #customers th{
      width: 13%;
      text-align: center;
  }
  
  #customers td, #customers th {
    border: 1px solid #ddd;
    padding: 3px;
    font-size: 0.8em;
  }
  
  #customers tr:nth-child(even){background-color: #f2f2f2;}
  #customers tr:nth-child(odd){background-color: #ada9a9;}
  
  #customers tr:hover {background-color: #ddd;}
  
  #customers th {
    padding-top: 5px;
    padding-bottom: 5px;
    text-align: left;
    background-color: #4CAF50;
    color: white;
  }

  .curent_{
      background-color: rgb(4, 128, 0) !important;
  }