.priereRow{
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-bottom: 1px solid rgba(175, 175, 175, 0.452);
    padding: 3px 8px ;
    margin: 0 8px;
}
.priereRow h5{
    font-size: 0.9em;
    color: white;
    font-weight: 300;
    flex: 1;
}

.left{
    text-align: left;
}
.right{
    text-align: right;
}
.center{
    text-align: center;
}

.curent{
    color: white;
    font-size: 1.1em;
    background-color:hsla(202, 91%, 13%, 0.712);
}