
  
.aya{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    color: rgba(252, 252, 181, 0.911);;
    margin: 15px 0;
    padding: 8px 8px ;
    width: 100%;
    font-family: 'MeQuran';
}
.aya h4, .aya h5{
    width: 100%;
    padding: 3px;
    
}
.aya h6{
    padding: 3px;
}
.aya p{
    border: 1px solid rgb(241, 241, 160);
    width: 100%;
    border-radius: 5px;
    padding: 10px;
    font-weight: 300;
    line-height: 1.4em;
}