
.navbar_wrapper{
    position: absolute;
    top:0;
    left:0;
    z-index: 2;
    width: 100vw;
    height: 100vh;
    opacity: 0;
    transform: translateX(-100%);
    background-color: rgba(80, 76, 76, 0.651);
    
  }
  
  #check{
    display: none;
  }
  #check:checked ~ .show{
    left: 330px;
    opacity: 0;
  }
  #check:checked ~ .cancel {
    left: 250px;
    opacity: 1;
  }
  
  #check:checked ~ .navbar{
    margin-left: 0;
  }
  
  #check:checked ~ .navbar_wrapper {
    z-index: 3;
    opacity: 0.5;
    left: 0px;
    transform: translateX(0);
  }
  
  
  .cancel{
    z-index: 9999;
    position: fixed;
    color: white;
    font-size: 2em;
    left: -30px;
    opacity: 0;
    top: 15px;
    transition: 0.4s;
    cursor: pointer;

  }
  
  .show{
    z-index: 9999;
    position: fixed;
    font-size: 1.4em;
    left: 15px;
    top: 15px;
    color:rgb(255, 255, 255);
    padding: 5px;
    border-radius: 5px;
    transition: 0.5s;
    cursor: pointer;

  }
  
 
  .navbar{
    z-index: 5;
    float: left;
    position: fixed;
    height: 100%;
    width: 300px;
    margin-left: -100%;
    background-color: #282c34;
    transition: margin 0.4s ;
  }
  .navbar header{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 10px;
    background-color: rgba(4, 50, 119, 0.952);
    cursor: pointer;
  }
  .navbar header h1{
    color: white;
    text-align: center;
    line-height: 65px;
    
    transition: 0.3s;
    position: relative;
  }
  .navbar header img{
    width: 30%;
    border-radius: 50%;
    box-shadow: 0 0 5px white;
  } 
  .navbar header:hover h1{
    font-size: 2.1em; 
  }
  .navbar ul{
    height: 100%;
   
  }
  
  .navbar ul li{
    list-style: none;
    text-align: left;
    line-height: 60px;
    padding-left: 16px;
    color: rgba(255, 255, 255, 0.685);
    transition: 0.3s ease;
    border-bottom: 1px solid rgba(255, 255, 255, 0.671);
    cursor: pointer;
  }
  .navbar ul li a{
    text-decoration: none;
    color: rgba(255, 255, 255, 0.685);
    display: block;
    width: 100%;
    height: 100%;
  }
  .navbar ul li span, .navbar ul li i{
    transition: 0.3s linear;
  }
   .navbar ul li:hover a{
    color: rgb(75, 219, 255);
  }
  .navbar ul li:hover i{
    color: rgb(75, 219, 255);
  }
  .navbar ul .contact{
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    z-index: 3;
    border-top: 1px solid rgba(255, 255, 255, 0.671);
  }
  
 
  
  .navbar ul li i{
    margin-right: 16px;
  }

  .gera_setting {
    position: fixed;
    top: 15px;
    right: 20px;

    z-index: 9999;
  }
  .gera_setting a i{
    color: white;
    font-size: 1.8em;
  }