.contact_container{
    padding-top: 60px;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.container{
    display: flex;
    justify-content: center;
    align-items: center;
}
.contact{
    border: 1px solid;
    position: relative;
    width: 40%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    color:white
}
.over_{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(17, 17, 17, 0.87);
    z-index: -1;
}
.contact img{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: -2;
}
.contact div h3 {
    text-align: left;
    margin-top: 30px;
    width: 200px;
    font-size: 1.2em;
}
.contact div p{
    text-align: left;
    margin-top: 10px;
    width: 200px;
    font-size: 1em;
    color: #d90427;
}
.contact div p a{
    cursor: pointer;
    color: #d90427;
    text-decoration: none;
}

.form h1{
    font-weight: 800;
    font-size: 2em;
    margin-bottom: 8px;
    color: #04d9ce;
}
@media screen and (max-width: 990px){
    .container{
        width: 100%;
        flex-direction: column-reverse;
    }
    .contact{
       display: none;
    }
    .form{
        width: 100%;
        height: 100vh;
        background-color: transparent;
    }
    .form form input,.form form textarea {
        width: 85vw;
        margin: 0 auto;
    }
  

}