.alert-warning{
    display: none;
    font-size: 0.9em;
    color: rgb(207, 105, 45);
}
.form {
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 30px 20px;
    height: 100vh;
    flex-direction: column;
    height: 100%;
}
 form input, form textarea {
    width: 400px;
    padding: 8px;
    border: 1px solid rgb(184, 182, 182);
    display: block;
    border-radius: 3px;
    box-shadow: 0 0 3px rgba(223, 223, 223, 0.212);
    color: rgb(90, 89, 89);
}
 form div label{
    display: block;
    text-align: left;
    padding: 10px 0 10px 0; 
    color: rgb(224, 224, 224);
    font-weight: 500;
    font-size: 1.1em;
}
form input:focus,form textarea:focus{
    background-color: white;
    box-shadow: 1px 1px 4px #c5c5c536;
}
form textarea{
    resize: none;
}
.form-submit{
    text-align: right;
    width: 100%;
}
#submit{
    background-color: #d9042700;
    color: #edf2f4;
    font-weight: 700;
    width: 100%;
    border: none;
    font-size: 1.5em;
    box-shadow: none;
    cursor: pointer;
    transition: .3s linear;
} 
#submit:hover{
    color: rgba(255, 99, 71, 0.692);
}
small{
    font-size: 0.7em;
    text-align: left;
    color: rgb(224, 222, 222);
}