.bienvenue{
    min-height: 100vh;
    background-size: 100% 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    background: linear-gradient(45deg, rgb(7, 46, 114), rgb(75, 130, 233));
}
.bienvenue img{
    width: 150px;
    border-radius: 50%;
    box-shadow: 0 0 10px rgb(255, 255, 255), 0px 0px 100px rgb(41, 41, 41);
}
.h-bienvenue{
    color: white;
    margin-top: -70px;
    color: rgba(255, 255, 255, 0.767);
    font-weight: 900;
}

.b-bienvenue{
    background-color: rgba(0, 0, 0, 0);
    color: rgba(255, 255, 255, 0);
    border: 0;
    font-size: 1.5em;
    font-weight: 600; 
    position: relative;
    transition: .3s linear;
    position: relative;
    text-align: center;
}

.b-bienvenue::after{
    content: "Suivant";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    transform-origin: center;
    transform: scale(1.5);
    border-radius: 50%;
    font-weight: 600; 
    color: rgba(255, 255, 255, 0.808);
    animation: wave 2s ease-in-out infinite;
}

@keyframes wave{
    0%{
        transform: scale(1.5);
        color: rgba(255, 255, 255, 0);
    }
    100%{
        transform: scale(1);
        color: #ffffffb9;
    }
}

.b-bienvenue:hover{
    color: rgba(145, 179, 142, 0.9);
    transform: scale(1.3);
}
.b-bienvenue:hover::after{
    content: "";
}